.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  text-align: center;
  background-color: #222;
  padding: 8px;
  color: white;
}

.App-title {
  font-size: 15px;
}

.App-intro {
  font-size: large;
}

.Div-border-red{
  border-style: dashed;
  border-width: 2px;
  border-color: red;
}

.Div-border-yellow{
  border-style: dashed;
  border-width: 2px;
  border-color: yellow;
}

.Div-border-blue{
  border-style: dashed;
  border-width: 2px;
  border-color: blue;
}

.Div-border-green{
  border-style: dashed;
  border-width: 2px;
  border-color: green;
}

.Div-side1{
  float:left;
  width:46%;
  overflow:hidden;
}
.Div-side2{
  float:left;
  width:34%;
  overflow:hidden;
}
.Div-side3{
  float:left;
  width:19%;
  overflow:hidden;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.Font-size15px{
  font-size: 15px;
}


